@import './src/styles/variables';
@import './src/styles/mixins';
@import './src/styles/classes';

.UsersBlock {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 20px;
  display: inline-flex;
  flex-wrap: wrap;
  gap: 50px;

  .UserBlock {
    border: none;
    color: #1D2732;
    display: inline-flex;
    width: calc(100% * 1 / 6 - 50px);

    .UserItem {
      width: 100%;
      border: 1px solid black;
      border-radius: 4px;
      padding: 20px 15px;
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;

      .UserPhotoBlock img {
        max-width: 150px;
      }

      .UserEmailBlock {
        max-width: 160px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
    }

    @media screen and (max-width: 1420px) {
        width: calc(100% * 1 / 5 - 50px);
    }

    @media screen and (max-width: 1175px) {
        width: calc(100% * 1 / 3 - 50px);
    }

    @media screen and (max-width: 745px) {
        width: calc(100% * 1 / 2 - 50px);
    }

    @media screen and (max-width: 500px) {
        width: calc(100% * 1 / 1 - 50px);
    }
  }
}


/*=============UserProfile*/
.UserProfileWrap {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.UserProfileInfoWrap {
  margin-top: 50px;
  display: inline-flex;
  flex-direction: column;
  padding: 20px;
  border-radius: 8px;
  border: 1px solid;
}

.UserProfilePhoto {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.UserProfilePhoto img {
  max-width: 300px;
}

.UserProfileInfo {
  margin-top: 15px;
  display: inline-flex;
  flex-direction: column;
  gap: 7px;
}